@media screen and (min-width: 960px) {
  .main__slider-container {
    width: 960px;
    margin: 0 auto;
    padding-top: 4em; } }

@media screen and (max-width: 959px) {
  .main__slider-container {
    display: none;
    overflow: hidden; } }

.slider {
  height: 200px;
  width: 960px;
  box-sizing: content-box; }

.slider__screen {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden; }

.slider__lens {
  height: 100%;
  transition: all .0s ease-in-out; }

.slider__lens_transition {
  transition: all .9s ease-in-out; }

.slider__item {
  height: 200px;
  width: 960px;
  float: left;
  cursor: pointer; }

.hamburger {
  display: none; }

.hamburger__toggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 44px;
  height: 44px;
  overflow: hidden;
  cursor: pointer;
  transition: all 300ms ease-in-out; }

@media screen and (max-width: 768px) {
  .hamburger + label + .nav__left,
  .hamburger + label + .nav__left + .nav__right {
    display: none; }
  .hamburger:checked + label + .nav__left,
  .hamburger:checked + label + .nav__left + .nav__right {
    display: block; }
  .hamburger__toggle span {
    display: block;
    position: absolute;
    height: 3px;
    top: 20px;
    left: 4px;
    right: 4px;
    background: #767676; }
  .hamburger:checked + .hamburger__toggle span {
    background: none; }
  .hamburger__toggle span::after, .hamburger__toggle span::before {
    content: "";
    position: absolute;
    left: 0;
    height: 3px;
    width: 100%;
    background: #767676; }
  .hamburger__toggle span::before {
    top: -10px; }
  .hamburger__toggle span::after {
    bottom: -10px; }
  .hamburger:checked + .hamburger__toggle span::before {
    top: 0;
    transform: rotate(45deg); }
  .hamburger:checked + .hamburger__toggle span::after {
    bottom: 0;
    transform: rotate(-45deg); } }
