@media screen and (min-width: 960px){
	.main__slider-container{
		width: 960px;
		margin: 0 auto;
		padding-top: 4em;
	}
}
@media screen and (max-width: 959px) {
	.main__slider-container{
		display: none;
		overflow: hidden;
	}
}
.slider {
	height: 200px;
	width: 960px;
	box-sizing: content-box;
}

.slider__screen {
	position: relative;
	height: 100%;
	width: 100%;
	overflow: hidden;
}

.slider__lens {
	height: 100%;
	transition: all .0s ease-in-out;

}

.slider__lens_transition {
	transition: all .9s ease-in-out;
}

.slider__item {
	height: 200px;
	width: 960px;
	float: left;
	cursor: pointer;
}


